.auth-form {
    padding: 20px;
    width: 400px;
    margin: 100px auto;
    max-width: 90%;
    border: 1px solid #999;
    border-radius: 20px;
    box-shadow: 0 0 3px 1px rgba(0,0,0,0.2);
}

.auth-form__register-now {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
}